html, body {
  height: 100%;
  overflow: hidden;
}
@keyframes pulseBorder {
  0% {
    /* border: 4px solid transparent; */
    box-shadow: 0px 0px 0px black;
  }
  50% {
    /* border: 4px solid #fff;  */
    box-shadow: 0px 0px 20px black;
  }
  100% {
    /* border: 4px solid transparent; */
    box-shadow: 0px 0px 0px bla;
  }
}

.song-image-container {
  border-radius: 50%;
  overflow: hidden;
  position: relative;
}

.pulsating-border {
  animation: pulseBorder 2s infinite;
}

.scaling-image {
  transform-origin: center;
  transition: transform 0.2s ease-in-out;
}

.scaling-image.active {
  transform: scale(1.1);
}


.UserInfo {
  background: #ffffff0f;
  border-radius: 16px;
  box-shadow: inset 0px 0px 6px black;
  backdrop-filter: blur(4.4px);
  -webkit-backdrop-filter: blur(4.4px);
}
@keyframes fadeInAndRotate {
  0% {
    opacity: 0;
    transform: scale(2.2)  rotate(0deg);
  }
  /* 50% {
    opacity: 0.7;
    transform: translateX(0) rotate(180deg);
  } */
  100% {
    opacity: 0.8;
    transform: scale(1) rotate(360deg);
  }
}

/* Define the animation when the component enters */
.fade-enter {
  opacity: 0;
  transform:scale(2.2) (0deg);
}

.fade-enter-active {
  animation: fadeInAndRotate 0.8s linear;
  animation-fill-mode: forwards;
}

.load-enter{
  opacity: 0;
}
.load-enter-active{
  opacity: 0.6;
  transition: opacity 0.8s;
}
